@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.footer{display:flex;flex-direction:column;background-color:#282828;color:#F8F8F8;height:10vh;text-align:center;align-items:center;justify-content:center}.footer__text{font-size:0.7rem;font-family:'Roboto';color:gray}.footer__line{border-bottom:1px solid gray;width:240px;opacity:0.3}

.navigation{display:flex;height:30px}.navigation__list{display:flex;list-style:none;margin:0px;padding:0}.navigation__link-text{display:flex;text-decoration:none;color:#282828;font-size:0.9rem;margin-right:60px;padding-bottom:11px}@media only screen and (max-width: 900px){.navigation__link-text{margin-right:40px}}.active{color:#ED7533;border-bottom:3px solid #ED7533}

.logo{display:flex;flex-direction:column;width:120px;padding-top:40px}.logo__abc-row{display:flex;line-height:3rem;justify-content:space-between}.logo__icon{display:flex;width:20px;background-color:#ED7533;margin:0.2rem 0}.logo__abc-text{display:flex;font-size:3rem;letter-spacing:1px;font-weight:300;margin:0}.logo__cams-row{display:flex;justify-content:space-between;width:100%;font-weight:100}

.header{display:flex;flex-direction:column;border-bottom:1px solid #ccc;padding:0 270px;height:23vh;justify-content:space-between}@media only screen and (max-width: 900px){.header{padding:0 100px}}

.card{display:flex;flex-direction:column;border:none;box-shadow:-1px 0px 11px 2px lightgray;width:450px;height:400px;margin:auto;background-color:#fff;border-radius:5px}

.about{display:flex;flex-direction:row;padding:40px 270px}@media only screen and (max-width: 900px){.about{padding:20px}}.about__card{width:290px;height:300px}.about__image-container{width:100%;height:180px}.about__image{object-fit:cover;width:100%;height:100%}@media only screen and (max-width: 600px){.about__image{height:100%;width:100%}}.about__text-container{display:flex;flex-direction:column;padding:0 10px;height:100%}.about__title{font-size:1.4rem;color:gray}.about__text{font-size:0.8rem;margin-top:-10px;color:gray}

.select{display:flex;width:400px;border:1px solid #b7b7b7;height:35px;cursor:pointer;outline:0;margin-right:40px;font-family:'Roboto',sans-serif}.select__option{font-family:'Roboto',sans-serif;width:300px}

.spinner{display:flex;justify-content:center;align-items:center;width:100%;height:200px;z-index:1}.spinner__container{display:flex;width:40px;height:40px;background-color:#ED7533;animation:rotateplane 1.2s infinite ease-in-out}@keyframes rotateplane{0%{transform:perspective(120px) rotateX(0deg) rotateY(0deg)}50%{transform:perspective(120px) rotateX(-180deg) rotateY(-180deg)}100%{transform:perspective(120px) rotateX(-180deg) rotateY(-180deg)}}

.cams{display:flex;flex-direction:column;padding:40px 270px}@media only screen and (max-width: 900px){.cams{width:auto;height:auto;margin:auto auto;padding:0}}.cams__select{display:flex;flex-direction:row;padding-left:35px}@media only screen and (max-width: 900px){.cams__select{flex-direction:column;margin-left:15px}}.cams__image{display:flex;flex-direction:column;padding:50px}@media only screen and (max-width: 900px){.cams__image{padding-top:10px}}.cams__img{object-fit:cover;width:100%;height:100%;border-radius:5px;margin:auto auto}@media only screen and (max-width: 900px){.cams__img{width:auto;height:auto;margin:auto auto}}

.button{display:flex;background-color:#2F96F3;width:80px;height:30px;justify-content:center;align-items:center;margin-left:165px;color:#fff;letter-spacing:1px;font-size:0.8rem;border:none;margin-top:30px;box-shadow:1px 2px 1px 0px #ccc;border-radius:2px}

.input__container{position:relative}.card__input{display:flex;width:250px;border:none;border-bottom:1px solid #ccc;outline:0;font-size:1rem;color:#ccc;font-family:'Roboto'}.input__placeholder{position:absolute;bottom:30px;left:2px;pointer-events:none;font-size:1rem;color:#ccc}.input__mandatory-mark{color:red}.input__errors{display:block;color:red;font-size:0.7rem;justify-content:center;height:20px}

.form{display:flex;flex-direction:column;align-items:center;margin:auto}.form__textarea{display:flex;width:250px;border:none;border-bottom:1px solid #ccc;outline:0;font-size:1rem;color:#ccc;font-family:'Roboto';resize:none}.form__textarea::-webkit-input-placeholder{display:flex;color:#ccc;text-align:left;transform:translateY(55px)}.form__textarea:-ms-input-placeholder{display:flex;color:#ccc;text-align:left;transform:translateY(55px)}.form__textarea::placeholder{display:flex;color:#ccc;text-align:left;transform:translateY(55px)}

.main{background-color:#F8F8F8;display:flex;flex-direction:column;position:relative;min-height:67vh}


*{box-sizing:border-box}body{margin:0;font-family:'Roboto', sans-serif;font-size:20px}

